
.main {
  padding: 120px 0
}

.navbar {
  padding: 4.5rem 0rem;
  transition: padding .5s;
}

.scrolled-nav {
  background: $dark;
  padding: 2rem 0;
  transition: padding .5s;
  opacity: .85;
}

.scrolled-nav .logo {
  width: 50%;
  height: auto;
  transition: width .5s;
  transition: height .5s;
}

.carousel-control-prev {
  top: 55%;
}

.carousel-control-next {
  top: 55%;
}

.jumbotron {
  padding: 0px;
}
/* Header Parallax Element Style*/
.paral {
min-height: 600px;
background-attachment: fixed;
background-size: cover;
background-position: 50% 50%;
}
/* Paragraph for Parallax Section */
.paral p {
font-size: 24px;
color:#f5f5f5;
text-align: center;
line-height: 60px;
}

/* Heading for Parallax Section */
.paral h1 {
color: rgba(255, 255, 255, 1);
font-family: 'Dual-300', sans-serif;
font-size: 3.5rem;
letter-spacing: 5px;
text-align: center;
line-height: 50px;
}
.paral h2 {
color: $white;
font-family: 'Dual-300', sans-serif;
font-size: 3.7rem;
letter-spacing: 3px;
text-align: center;
line-height: 2.5rem;
}
.paral h3 {
color: $brand-red;
font-family: 'HelveticaNeue-Light', sans-serif;
font-size: 1rem;
letter-spacing: 3px;
text-align: center;
line-height: 30px;
text-transform: uppercase;
}

.paral h4 {
  color: $white;
font-family: 'HelveticaNeue-Thin', sans-serif;
font-size: 1.15rem;
letter-spacing: .15rem;
text-transform: uppercase;
}

.paral h5 {
  color: $white;
  font-family: 'HelveticaNeue-CondensedBlack', sans-serif;
  letter-spacing: .15rem;
  text-transform: uppercase;
  text-align: left;
  font-size: 3rem;
  line-height: 3rem;
}

.paral h6 {
  color: $white;
  font-family: 'HelveticaNeue-Light', sans-serif;
  font-size: .9rem;
  letter-spacing: .15rem;
   padding: .75rem 0 0 3.5rem;
  text-transform: uppercase;

}



.carousel h3 {
  color: $white;
  font-family: 'HelveticaNeue-CondensedBlack', sans-serif;

  text-align: left;
  font-size: 3rem;
  line-height: 3rem;
}


/* Image for Parallax Section */
.paralsec {
background: url(../images/hero_01.jpg) no-repeat;
background-size: 100% 80%;
}

.paralsec1 {
background: url("https://img.webnots.com/2017/05/parallax1.jpg");
}

.paralsec2 {
background-image: url("https://img.webnots.com/2015/11/parallax2.jpg");
}

/* Remove Bottom Margin from Jumbotron */
.jumbotron{
  margin: -120px 0 0 0;
  padding: 160px 0 0;
}

/* Add more images for more sections */
.jumbotron-fluid {
  background: url(../images/hero_01.jpg) 10px 10px no-repeat !important;

}
.navbar h1 {
